export const GetXLabel = (startDate, endDate, formatType) => {
  var currentMonth = moment(startDate).startOf("month").format("MMM");
  var xLabel = [];
  switch (formatType) {
    case "30days":
      xLabel = ["Week 1", "Week 2", "Week 3", "Week 4"];
      break;
    case "6months":
      //xLabel.push(currentMonth);
      for (var i = 1; i <= 3; i++) {
        currentMonth = moment(startDate).add(i, "month").startOf("month").format("MMM");
        xLabel.push(currentMonth);
      }
      break;
    case "1year":
      //xLabel.push(currentMonth);
      for (var i = 1; i <= 12; i++) {
        currentMonth = moment(startDate).add(i, "month").startOf("month").format("MMM");
        xLabel.push(currentMonth);
      }
      break;
    default:
      break;
  }
  return xLabel;
};

export const GetWeekWiseDates = (startDate, endDate) => {
  let result = [];
  let _enddate = moment(endDate, "MM/DD/YYYY");
  let diff = betweenDays({ startdate: moment(startDate, "MM/DD/YYYY"), enddate: moment(_enddate, "MM/DD/YYYY") });
  while (diff > 6) {
    result.push({
      start: moment(_enddate).startOf("week").format("MM/DD/YYYY"),
      end: moment(_enddate).endOf("week").format("MM/DD/YYYY"),
    });
    _enddate = moment(_enddate).subtract(7, "days");
    diff = betweenDays({ startdate: moment(startDate), enddate: moment(_enddate) });
  }
  if (diff > 0) {
    result.push({
      start: moment(startDate, "MM/DD/YYYY").format("MM/DD/YYYY"),
      end: moment(startDate).endOf("week").format("MM/DD/YYYY"),
    });
  }
  return result.reverse().map((x) => ({ ...x, text: moment(x.end).format("MM/DD") }));
};

export const GetXLabelObjects = (startDate, endDate, formatType, monthBack) => {
  var xLabel = [],
    temp = [];
  switch (formatType) {
    case "30days":
      temp = ["Week 1", "Week 2", "Week 3", "Week 4"];
      if (monthBack) {
        temp.forEach((x, i) => {
          const start = moment(startDate)
            .subtract(i + 1, "weeks")
            .startOf("week")
            .format("MM/DD/YYYY");
          const end = moment(startDate)
            .subtract(i + 1, "weeks")
            .endOf("week")
            .format("MM/DD/YYYY");
          const endDate = moment(startDate)
            .subtract(i + 1, "weeks")
            .endOf("week")
            .format("MM/DD");
          xLabel.push({ text: `Week (${endDate})`, start, end });
        });
      } else {
        temp.forEach((x, i) => {
          const start = moment(startDate)
            .add(i + 1, "weeks")
            .startOf("week")
            .format("MM/DD/YYYY");
          const end = moment(startDate)
            .add(i + 1, "weeks")
            .endOf("week")
            .format("MM/DD/YYYY");
          const endDate = moment(startDate)
            .add(i + 1, "weeks")
            .endOf("week")
            .format("MM/DD");
          xLabel.push({ text: `Week (${endDate})`, start, end });
        });
      }
      break;
    case "6months":
      for (var i = 1; i <= 3; i++) {
        const text = moment(startDate).add(i, "months").startOf("month").format("MMM");
        const start = moment(startDate).add(i, "months").startOf("month").format("MM/DD/YYYY");
        const end = moment(startDate).add(i, "months").endOf("month").format("MM/DD/YYYY");
        xLabel.push({ text: text, start, end });
      }
      break;
    case "1year":
      for (var i = 1; i <= 12; i++) {
        const text = moment(startDate).add(i, "months").startOf("month").format("MMM");
        const start = moment(startDate).add(i, "months").startOf("month").format("MM/DD/YYYY");
        const end = moment(startDate).add(i, "months").endOf("month").format("MM/DD/YYYY");
        xLabel.push({ text: text, start, end });
      }
      break;
    default:
      break;
  }
  return xLabel;
};

export const GetYLabel = (startDate, endDate, formatType) => {
  var yLabel = [];
  switch (formatType) {
    case "30days":
      yLabel.push({
        startDate: moment(startDate).toDate().getTime(),
        endDate: moment(startDate).endOf("week").toDate().getTime(),
      });

      for (var i = 1; i <= 2; i++) {
        yLabel.push({
          startDate: moment(startDate).add(i, "week").startOf("week").toDate().getTime(),
          endDate: moment(startDate).add(i, "week").endOf("week").toDate().getTime(),
        });
      }
      yLabel.push({
        startDate: moment(endDate).startOf("week").toDate().getTime(),
        endDate: moment(endDate).toDate().getTime(),
      });

      break;
    case "6months":
      yLabel.push({
        startDate: moment(startDate).startOf("month").toDate().getTime(),
        endDate: moment(startDate).add(i, "month").endOf("week").toDate().getTime(),
      });
      for (var i = 1; i <= 1; i++) {
        yLabel.push({
          startDate: moment(startDate).add(i, "month").startOf("month").toDate().getTime(),
          endDate: moment(startDate).add(i, "month").endOf("month").toDate().getTime(),
        });
      }
      yLabel.push({
        startDate: moment(endDate).startOf("month").toDate().getTime(),
        endDate: moment(endDate).endOf("month").toDate().getTime(),
      });
      break;
    case "1year":
      yLabel.push({
        startDate: moment(startDate).startOf("month").toDate().getTime(),
        endDate: moment(startDate).add(i, "month").endOf("week").toDate().getTime(),
      });
      for (var i = 1; i <= 10; i++) {
        yLabel.push({
          startDate: moment(startDate).add(i, "month").startOf("month").toDate().getTime(),
          endDate: moment(startDate).add(i, "month").endOf("month").toDate().getTime(),
        });
      }
      yLabel.push({
        startDate: moment(endDate).startOf("month").toDate().getTime(),
        endDate: moment(endDate).endOf("month").toDate().getTime(),
      });
      break;
    default:
      break;
  }
  return yLabel;
};
