/* eslint-disable react-hooks/exhaustive-deps */
import { instance } from "actions/axiosInstance";
import "assets/css/paymentdetails.css";
import TextAreaModal from "components/Popups/TextArea";
import { handleAPIError } from "handlers/setters";
import get from "lodash/get";
import uniqBy from "lodash/uniqBy";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { getFormattedDate } from "utils/dates";
import { resetconfirmationboxData, setconfirmationboxData } from "../ConfirmationBox";
import TextareaEditorCell from "components/Table/TextareaEditorCell";
import Toaster from "../Toaster";

function addURL(type) {
  let res = "";
  type === "payables" && (res = `supplier/note`);
  type === "receivables" && (res = `customer/note`);
  type === "vendors" && (res = `supplier/note`);
  type === "customers" && (res = `customer/note`);
  return res;
}

function getURL(type, id) {
  let res = "";
  type === "payables" && (res = `supplier/note?per_page=1000&invoice_id=${id}`);
  type === "receivables" && (res = `customer/note?per_page=1000&invoice_id=${id}`);
  type === "vendors" && (res = `supplier/note?per_page=1000&supplier_id=${id}`);
  type === "customers" && (res = `customer/note?per_page=1000&customer_id=${id}`);
  return res;
}

function putURL(type, id) {
  let res = "";
  type === "payables" && (res = `supplier/note?id=${id}`);
  type === "receivables" && (res = `customer/note?id=${id}`);
  type === "vendors" && (res = `supplier/note?id=${id}`);
  type === "customers" && (res = `customer/note?id=${id}`);
  return res;
}

function deleteURL(type, id) {
  let res = "";
  type === "payables" && (res = `supplier/note/delete?id=${id}`);
  type === "receivables" && (res = `customer/note/delete?id=${id}`);
  type === "vendors" && (res = `supplier/note/delete?id=${id}`);
  type === "customers" && (res = `customer/note/delete?id=${id}`);
  return res;
}

const keyObject = {
  payables: "supplier_id",
  receivables: "customer_id",
  vendors: "supplier_id",
  customers: "customer_id"
};

// payables, receivables, vendors, customers
function NotesList(props) {
  const { type = "", id, data, notesUpdated = false, setnotesUpdated } = props;
  const [notes, setnotes] = useState(null);
  const [showNoteModal, setshowNoteModal] = useState(false);
  const url = getURL(type, id);

  useEffect(() => {
    if (!notes || notesUpdated) {
      if (!id) { 
        return;
      }
      refreshNotes();
      notesUpdated && setnotesUpdated && setnotesUpdated(false);
    }
  }, [id, notesUpdated, notes]);

  function refreshNotes() {
    instance
      .get(url)
      .then(resp => {
        const allPromises = uniqBy(get(resp, "data.result", []), "followed_by").map(x =>
          instance.get(`/client/user?id=${x.followed_by}`)
        );
        let DATA = get(resp, "data.result", []);
        Promise.all(allPromises).then(resp => {
          DATA = DATA.map((x, i) => ({
            ...x,
            followed_by_name: get(
              resp.find(r => get(r, `data.result[0].id`, "") === x.followed_by),
              "data.result[0].username",
              ""
            )
          }));
          setnotes(DATA.reverse());
        });
      })
      .catch(e => handleAPIError(e));
  }

  return (
    <div className="row notes-wrapper">
      <TextAreaModal
        header="Add Note"
        show={showNoteModal}
        handleClose={() => setshowNoteModal(false)}
        handleSubmit={value => {
          const key = keyObject[type];
          const custumerOrVendorId = get(data, "customer.id", "") || get(data, "supplier.id", "") || id;
          let postdata;
          if (type === "payables" || type === "receivables") {
            postdata = {
              date: moment().format("MM/DD/YYYY"),
              note: value,
              invoice_id: JSON.stringify(id),
              [key]: custumerOrVendorId
            };
          } else {
            postdata = {
              date: moment().format("MM/DD/YYYY"),
              note: value,
              [key]: custumerOrVendorId
            };
          }
          instance
            .put(addURL(type), postdata)
            .then(() => {
              setshowNoteModal(false);
              Toaster("Notes added", "success");
              refreshNotes();
            });
        }}
      />
      {notes && notes.length ? (
        uniqBy(notes || [], "note").map((note, index) => (
          <div className="mb-2 col-md-12" id="note">
            <div className="row text-secondary border-bottom">
              <div className="col-md-12">
                <div className="row">
                  <div className="col-md-7 text-left">
                    <i className="fa fa-calendar" aria-hidden="true"></i> {getFormattedDate(new Date(note.date))}
                  </div>
                  <div className="col-md-5">
                    <i
                      className="fa fa-trash-o ml-5 cursor-pointer"
                      style={{
                        position: "absolute",
                        right: 8
                      }}
                      aria-hidden="true"
                      onClick={() => {
                        setconfirmationboxData({
                          variant: "warning",
                          msg: "Are you sure you want to delete?",
                          onSave: () => {
                            resetconfirmationboxData();
                            instance
                              .get(url)
                              .then(resp => {
                                const tuples = get(resp, "data.result", []).filter(
                                  record => record.note === note.note && record.date === note.date
                                );
                                const allpromises = tuples.map(record =>
                                  instance.put(
                                    deleteURL(type, record.id),
                                    {
                                      date: moment().format("MM/DD/YYYY")
                                    }
                                  )
                                );
                                Promise.all(allpromises).then(() => {
                                  Toaster("Deleted successfully", "success");
                                  refreshNotes();
                                });
                              });
                          }
                        });
                      }}
                    />
                  </div>
                </div>
              </div>
              <div className="col-md-12 pt-2 pb-2">
                <TextareaEditorCell
                  value={note.note}
                  updateValue={value => {
                    const key = keyObject[type];
                    const custumerOrVendorId = get(props, "id", "");

                    instance
                      .get(url)
                      .then(resp => {
                        const tuples = get(resp, "data.result", []).filter(
                          record => record.note === note.note && record.date === note.date
                        );
                        const allpromises = tuples.map(record =>
                          instance.put(
                            putURL(type, note.id),
                            {
                              date: moment().format("MM/DD/YYYY"),
                              note: value,
                              invoice_id: record.invoice_id,
                              [key]: custumerOrVendorId
                            }
                          )
                        );
                        Promise.all(allpromises).then(() => {
                          Toaster("Updated successfully", "success");
                          refreshNotes();
                        });
                      });
                  }}
                />
              </div>
              <div className="col-md-12 my-2">
                <i className="fa fa-user" aria-hidden="true"></i> {note.followed_by_name}
              </div>
            </div>
          </div>
        ))
      ) : (
        <div className="p-2">No Data</div>
      )}
    </div>
  );
}

const mapStateToProps = state => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(NotesList);
